
import './App.css';

import Navbar from './components/Navbar';

import News from './components/News'

import React, { Component } from 'react'

import {Switch} from "react-router-dom";

import {
    BrowserRouter as Router,
    Route
  } from "react-router-dom";
  

export default class App extends Component {
  pageSize= 15;
  render() {
    return (
      <div>
        <Router>
        <Navbar/>
          <Switch>
            <Route exact path="/"><News key="general" pageSize={this.pageSize} country='in' heading='General' category='general'/></Route>
            <Route exact path="/Business"><News key="business" pageSize={this.pageSize} country='in' heading='Business' category='business'/></Route>
            <Route exact path="/Entertainment"><News key="entertainment" pageSize={this.pageSize} heading='Entertainment' country='in' category='entertainment'/></Route>
            <Route exact path="/Health"><News key="health" pageSize={this.pageSize} country='in' heading='Health' category='health'/></Route>
            <Route exact path="/Science"><News key="science" pageSize={this.pageSize} country='in' heading='Science' category='science'/></Route>
            <Route exact path="/Sports"><News key="sports" pageSize={this.pageSize} country='in' heading='Sports' category='sports'/></Route>
            <Route exact path="/Technology"><News key="technology" pageSize={this.pageSize} country='in' heading='Technology' category='technology'/></Route>
          </Switch>
        </Router>
      </div>
    )
  }
}
