import React, { Component } from 'react'

import NewsItem from './NewsItem'

import Spinner from './Spinner'

import PropTypes from 'prop-types'

import InfiniteScroll from 'react-infinite-scroll-component'


export class News extends Component {

    static defaultProps = {
        country: 'in',
        pageSize: 8,
        category: 'general'
    }
    
    static propTypes = {
        country: PropTypes.string,
        pageSize: PropTypes.number,
        category: PropTypes.string
    }
   
    constructor(){
        super();
        // console.log("this is constractor of news component");

        this.state = {
            articles: [],
            loading: true,
            page:1,
            totalResults: 0
        }
    }

    // async updateNews(pageNo){
    //     const url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=71c48c3b64894f58bc55a11a7b1187a5&page=${this.state.page}pageSize=${this.props.pageSize}`;

    //     this.setState({
    //         loading:true
    //     })

    //     let data = await fetch(url);
    //     let parsedData = await data.json();
    //     // console.log(parsedData);

    //     this.setState({
    //         articles: parsedData.articles, 
    //         totalResults: parsedData.totalResults,
    //         loading:false
    //     });

        
    // }

    async componentDidMount(){
        let url=`https://newsapi.org/v2/top-headlines?counter=techcrunch&country=${this.props.country}&category=${this.props.category}apiKey=e3f373c735584346b9c36e16ceb13ce0&page=${this.state.page - 1}&pageSize=${this.props.pageSize}`;
        this.setState({
            loading:true
        })
        let data = await fetch(url);
        let parsedData = await data.json();
        // console.log(parsedData);
        this.setState({
            articles: parsedData.articles, 
            totalResults: parsedData.totalResults,
            loading:false
        });

        // this.updateNews();
    }


    handlePreviousClick=async()=>{
        console.log("previous");
        let url=`https://newsapi.org/v2/top-headlines?counter=techcrunch&country=${this.props.country}&category=${this.props.category}apiKey=e3f373c735584346b9c36e16ceb13ce0&page=${this.state.page - 1}&pageSize=${this.props.pageSize}`;
        
        this.setState({
            loading:true
        })
        let data = await fetch(url);
        let parsedData = await data.json();
        // console.log(parsedData);

        this.setState({
            page: this.state.page - 1,
            articles: parsedData.articles,
            loading:false,
            totalResults: parsedData.totalResults
        })
        // this.setState({page: this.state.page - 1})
        // this.updateNews();
        
    }

    handleNextClick=async()=>{
        console.log("Next");

        if(Math.ceil(this.state.page+1 < this.state.totalResults/this.props.pageSize)){

        let url=`https://newsapi.org/v2/top-headlines?counter=techcrunch&country=${this.props.country}&category=${this.props.category}apiKey=e3f373c735584346b9c36e16ceb13ce0&page=${this.state.page + 1}&pageSize=${this.props.pageSize}`;
        this.setState({
            loading:true
        })
        let data = await fetch(url);
        let parsedData = await data.json();
        // console.log(parsedData);



        this.setState({
            page: this.state.page + 1,
            articles: parsedData.articles,
            loading: false,
            totalResults: parsedData.totalResults
        })
        }
        else{

        }

        // this.setState({page: this.state.page + 1})
        // this.updateNews();
    }

    fetchMoreData = () => {
        // a fake async api call like which sends
        // 20 more records in 1.5 secs
        setTimeout(() => {
          this.setState({
            items: this.state.items.concat(Array.from({ length: 20 }))
          });
        }, 1500);
      };

    render() {
        return (
            <div className="container my-3">
                <h1 className="mx-4">Daily News - Top headline of {this.props.heading}</h1>
                {/* {this.state.loading && <Spinner/>} */}
                {/* this.state.loading &&  */}

                <InfiniteScroll>
                dataLength={this.state.articles.length}
                next={this.fetchMoreData}
                hasMore={this.state.articles.length !== this.totalResults}
                loader={<Spinner/>}

                <div className="row">

                {this.state.articles.map((element)=>{
                    return          <div className="col-md-4" key={element.url} >
                                        <NewsItem title={element.title?element.title.slice(0, 18):""} description={element.description?element.description.slice(0, 48):""} newsURL={element.url} imgurl={element.urlToImage?element.urlToImage:"https://s.abcnews.com/images/International/WireAP_8e3d80a2cbce4ee4a987c79621f87e92_16x9_992.jpg"} author={element.author} date={element.publishedAt} />
                                    </div> 
                                
                })}
                </div>

                </InfiniteScroll>
                <div className="container d-flex justify-content-between">
                    <button disabled={this.state.page<=1} type="button" className="btn btn-danger" onClick={this.handlePreviousClick}>&larr; Previous</button>
                    <button disabled={this.state.page+1 > this.state.totalResults/this.props.pageSize} type="button" className="btn btn-danger" onClick={this.handleNextClick}>Next &rarr;</button>
                </div>
            </div>
        )
    }
}

export default News
